import Side from '@/components/side'
import Header from '@/components/header'
export default {
  data() {
    return {
    };
  },
  components:{
    Side,
    Header
  },
  methods: {

  }
}
</script>

<style scoped>
@import "../../assets/styles/person.less";
</style>



